<template>
    <div class="wiki-mine-mian-list">
        <ul class="wiki-mine-mian-list-content">
            <li
                v-for="item in articleList"
                :key="item.id"
            >
                <p>
                    <router-link 
                        class="mine-mian-list-content-title"
                        tag="a"
                        :to="{path:'/article',query:{row: JSON.stringify(item)}}"
                    >{{item.title}}</router-link>
                    <span class="mine-mian-list-content-time">{{item.time}}</span>
                </p>
                <div class="mine-mian-list-content-people">
                    <p>{{'张三'}}</p>
                </div>
                <p class="mine-mian-list-content-article">{{item.content[0].content}}</p>
                <div class="mine-mian-list-content-attr">          
                    <a 
                        href="javascript:;" 
                        class="mine-mian-list-content-attr-delete"
                        @click="handledelete(item.id)"
                        >
                        <i class="el-icon-delete">删除</i>
                    </a>
                    <el-button type="primary" icon="el-icon-edit" title="点击修改文章" @click="gotoAritcle(item)"></el-button>
                </div>
            </li>
        </ul>
        <div class="block">
            <el-pagination
                layout="prev, pager, next"
                :total="allPage"
                :page-size=3
                @current-change="togglePage"
                >
            </el-pagination>
        </div>
    </div>
</template>

<script>
import * as ajax from '@/api'

export default {
    data(){
        return{
            articleList:[],
            allPage:1
        }
    },
    created(){
        ajax.getMyArticleList(1).then(resp=>{
            console.log(resp)
            if(resp.data.code){
                this.articleList = resp.data.data.article_info
                this.allPage = resp.data.data.page_info.totalPage*3
            }
        }) //默认请求第一页数据
    },
    components:{
        
    },
    methods:{
        togglecomment(){
            
        },
        togglePage(currentPage){
            ajax.getMyArticleList(currentPage).then(resp=>{
                if(resp.data.code){
                    this.articleList = resp.data.data.article_info
                    this.allPage = resp.data.data.page_info.totalPage*3 
                }
            })
        },
        gotoAritcle(item){
            this.$router.push({path:'/article', query:{row: JSON.stringify(item)}})
        },
        handledelete(id){
            if(confirm('确定要删除这篇文章吗？')){
                ajax.handleDeleteArticle(id).then(resp=>{
                    console.log(resp)
                    if(resp.data.code){
                        alert('删除成功')
                        this.articleList = this.articleList.filter(item=>{
                            return item.id !== id
                        })
                    }else{
                        this.$message.error(resp.data.msg + '请重试')
                    }
                })
            }
        },
    }
}
</script>

<style lang='scss'>
    .wiki-mine-mian-list{
        width: 900px;
        height: auto;
        background-color: #fff;
        &-content{
            width: 900px;
            list-style: none;
            padding: 0 20px;
            li{
                height: 250px;
                border-bottom: 1px solid rgb(199, 195, 195);
                padding-top: 20px;
                
                p{
                    .mine-mian-list-content-title{
                        font-size: 18px;
                        font-weight: bold;
                        color: #000;
                    }
                    .mine-mian-list-content-time{
                        float: right;
                        font-size: 14px;
                    }
                    margin-bottom: 5px;
                }
                .mine-mian-list-content-people{
                     .mine-mian-list-content-head-sculpture{
                        width: 45px;
                        height: 45px;
                        float: left;
                        margin-right: 10px;
                    }
                    p{
                        height: 45px;
                        line-height: 45px;
                    }
                }
                .mine-mian-list-content-attr{
                    position:relative;
                    bottom: -55px;
                    width: 100%;
                    .el-button{
                    width: 55px;
                    height: 30px;
                    line-height: 30px;
                        .el-icon-edit::before{
                            line-height: 10px;
                            position: absolute;
                            top: 10px;
                            left: 20px;
                        }
                    }
                    a{
                        color: #666;
                        float: right;
                        .commnt-icon{
                            font-size: 26px;
                        }
                    }
                    .mine-mian-list-content-attr-delete{
                        font-size: 19px;
                        height: 30px;
                        line-height: 30px;
                        margin-right: 20px;
                    }
                    
                }
            }
            .mine-mian-list-content-article{
                font-size: 15px;
                line-height: 25px;
                text-indent:20px;
                margin-top: 10px;
                display: -webkit-box;
                -webkit-box-orient: vertical;
                -webkit-line-clamp: 2;
                overflow: hidden;
            }
        }
        .block{
            margin-bottom: 30px;
            .el-pagination{
                text-align: center;
            }
        }
    }
</style>
